
/* Lớp overlay */
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5); 
    z-index: 999; 
}


.search-container {
    background: linear-gradient(to right, black, #3b0000);
    padding: 20px 0;
}

.search-bar {
    display: flex;
    gap: 10px;
    max-width: 900px;
    margin: auto;
}

.dropdowns {
    position: relative;
}

.search-dropdown {
    background: white;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 16px;
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
}


.search-dropdown-menu {
    position: absolute;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    min-width: 200px;
    list-style: none;
    padding: 0;
    margin-top: 5px;
    display: block;
    z-index: 1000;
}

.search-dropdown-item {
    color: #ffffff;
    padding: 10px 15px;
    font-size: 16px;
    display: block;
    text-decoration: none;
}

.search-dropdown-item:hover {
    background-color: #fff5f5;
}

.search-input {
    flex: 1;
    font-size: 16px;
    padding: 10px 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
}

.search-btn {
    background: #ED1B2F;
    color: white;
    font-size: 16px;
    padding: 10px 20px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    gap: 8px;
}
@media (max-width: 1179px) {
    .search-bar {
        flex-direction: column; 
        gap: 10px;
    }

    .search-input-group {
        display: flex;
        gap: 10px;
        width: 100%;
    }

    .search-input,.search-btn  {
        flex: 1; 
    }

  
    .dropdowns {
        width: 100%; 
        
    }
}
