.range-slider {
    width: 100%;
    background: transparent;
    position: absolute;
    pointer-events: none;
    z-index: 2;
    accent-color: rgb(27, 162, 6);
   
}

.range-slider::-webkit-slider-thumb {
    pointer-events: auto;
    appearance: none;
    width: 16px;
    height: 16px;
    background: #14f06c;
    border-radius: 50%;
    cursor: pointer;
}
.text-start {
    text-align: left !important;
    width: 100%;
    display: block;
}
.filter-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: white;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

.filters {
    display: flex;
    gap: 10px;
}

.filter-item {
    position: relative;
}

.filter-btn {
    background: white;
    border: 1px solid #ddd;
    border-radius: 20px;
    padding: 10px 15px;
    font-size: 14px;
    display: flex;
    align-items: center;
    gap: 5px;
    font-weight: 500;
    color: rgb(101, 101, 101);
}

.filter-control {
    display: flex;
    align-items: center;
    gap: 8px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 10px 15px;
    font-size: 14px;
    font-weight: 500;
    color: black;
}

.filter-btn:hover {
    border: 1px solid #000;
    color: #000;
}


.drop-industry {
    height: 260px;
    overflow-y: auto;
    overflow-x: hidden;
    max-height: 260px;
    scrollbar-width: thin;
    scrollbar-color: #ccc #f0f0f0;
}

.drop-header {
    padding: 10px;
    background: white;
    position: sticky;
    top: -10px;
    z-index: 10;
    border-bottom: 1px solid #ddd;
}

.drop-item {
    padding: 8px 12px;
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
}

.drop-item:hover {
    background-color: #f0f0f0;
}

.drop-search {
    width: 100%;
    padding: 6px;
    margin-bottom: 6px;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.drop-level,
.drop-working-models,
.drop-salary,
.drop-industry {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    background-color: white;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    padding: 10px;
    width: 300px;
}

.checkbox-level {
    width: 25px;
    height: 25px;
}

.btn-salary {
    border: 1px solid red;
    outline: none;
    color: red;
    border-radius: 5px;
}

.btn-salary:hover {
    opacity: 0.6;
}

.modal-dialog {
    max-width: 800px;
    width: 800px;
    height: 92%;
}

.modal-body {
    overflow-y: auto;
}

.modal-content {
    height: 100%;
}

.btn-filter-skill {
    border: 1px solid gray;
    border-radius: 25px;
    background-color: white;
}

.btn-filter-skill:hover {
    border: 1px solid black;
}

.input-range {
    border: 1px solid gray;
    border-radius: 25px;
    padding: 3px;
}



.btn-reset-filter {
    border: none;
    outline: none;
    background-color: white;
    color: #0048ff;
    font-weight: 600;
}

.btn-apply-filter {
    border: none;
    outline: none;
    padding: 10px 80px;
    background-color: #ed1b2f;
    color: white;
    border-radius: 5px;
}

.btn-apply-filter:hover {
    opacity: 0.8;
}

.btn-filter-skill:hover {
    border: 1px solid #ed1b2f;
    background-color: #FFDFDF;
    color: #ed1b2f;
}
.filter-industry {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    width: 720px;
    overflow: hidden;
    background: white;
}

.filter-header {
    margin-bottom: 10px;
}

.filter-search {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
}

.filter-list {
    height: 200px;
    max-width: 900px;
    overflow-y: auto;
    border-top: 1px solid #ddd;
    padding-top: 5px;
}

.filter-item {
    display: flex;
    align-items: center;
    padding: 5px;
    text-decoration: none;
    color: #333;
    font-size: 14px;
}


.filter-list::-webkit-scrollbar {
    width: 6px;
}

.filter-list::-webkit-scrollbar-thumb {
    background-color: #bbb;
    border-radius: 5px;
}

.filter-list::-webkit-scrollbar-thumb:hover {
    background-color: #888;
}

@media (max-width: 1179px) {
    .filters {
        display: none; 
    }
    .filter-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #f7f7f7;
        padding: 15px;
        border-radius: 100px solid #000000;
       
    }
    .filter-control {
        display: flex;
        justify-content: center;
        width: 100%; 
        text-align: center;
        padding: 15px;
        font-size: 16px;
        font-weight: bold;
        background-color: #ffffff;
        color: rgb(0, 0, 0);
        border-radius: 10px solid #000000;
    }

    .filter-control:hover {
        opacity: 0.8;
    }
    .modal-dialog {
        
        width: 700px;
        height: 90%;
    }
    
}
